div[class^="col-"] {
  align-items: center;
}

.text-platzi {
  color: #97c93e !important;
}

.btn-platzi {
  background-color: #97c93e;
  color: white;
}

.btn-platzi:hover {
  background-color: #82ad36;
}

#header img {
  width: 40px;
  height: 40px;
}

#main .carousel-inner img {
  max-height: 70vh;
  object-fit: cover;
  filter: grayscale(70%);
}

#carrusel {
  position: relative;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  /*z-index: 2;*/
}

.overlay .container,
.overlay .row {
  height: 100%;
}

.Organizadores img {
  max-height: 40vh;
  object-fit: cover;
}

.date img {
  max-width: 100%;
  max-height: 70vh;
  object-fit: cover;
}

.date {
  background-color: #343a40;
  color: white;
}

#mapa {
  max-width: 100%;
}

#footer {
  background: linear-gradient(90deg, #1c3643, #273b47 25%, #1e5372);
}

#footer a {
  color: white;
  text-decoration: none;
}

#footer a:hover {
  color: white;
  text-decoration: underline;
}

div[class^="col-"] {
  align-items: center;
}
